import { base_url } from "@/utils/const";
import Head from "next/head";
import { useEffect, useRef } from "react";

let observer: MutationObserver | undefined = undefined;

function nodeIsInput(node: Node): node is HTMLInputElement {
  return node.nodeName.toLocaleLowerCase() === "input";
}
export default function EnableExtention() {
  const inputRef = useRef<HTMLInputElement>(null);
  useEffect(() => {
    if (!inputRef.current) return;
    const queryAuto = new URLSearchParams(window.location.search).get("auto");
    if (queryAuto == "0") {
      return;
    }

    if (!observer) {
      observer = new MutationObserver((records) => {
        records.forEach((record) => {
          if (nodeIsInput(record.target) && record.target.value == "true") {
            window.location.href = base_url + "/ios/step-3/";
          }
        });
      });
    }
    observer.observe(inputRef.current, {
      attributes: true,
      attributeFilter: ["value"],
    });
    return () => {
      observer?.disconnect();
    };
  }, [inputRef.current]);
  return (
    <>
      <Head>
        <meta name="immersiveTranslateIosOnBoarding" />
      </Head>
      <input ref={inputRef} id="immersiveTranslateDetectedSlot" hidden />
    </>
  );
}
