import classNames from "classnames";
import styles from "./step.module.css";
import { base_url } from "@/utils/const";
import React from "react";

export type StepConfig = { step: number, name: string, href: string }
const defaultConfigs: StepConfig[] = [
  { step: 1, name: "固定插件", href: base_url + "/" },
  { step: 2, name: "翻译网页", href: base_url + "/step-2/" },
  { step: 3, name: "开始使用", href: base_url + "/step-3/" },
];
export default function Step({ step, configs }: { step: number, configs?: StepConfig[] }) {
  const curConfigs = configs || defaultConfigs;

  return (
    <div className={classNames(styles.container, "notranslate")}>
      {
        curConfigs.map((item, index) => {
          const isActive = item.step == step;
          return (
            <React.Fragment key={item.name}>
              <a className={classNames({
                [styles["step"]]: true,
                [styles["step-active"]]: isActive,
              })}
                href={item.href}
              >
                <div
                  className={classNames({
                    [styles["step-circle"]]: true,
                    [styles["step-circle-active"]]: isActive,
                  })}>
                  {item.step}
                </div>
                <span className={styles.label}>{item.name}</span>
              </a>
              {index < curConfigs.length - 1 && <div className={classNames({
                [styles.line]: true,
                [styles.lineActive]: isActive,
              })} />}
            </React.Fragment>
          );
        })
      }
    </div>
  );
}
